html * {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html body {
  font-size: 14px;
  min-width: 320px;
  font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif; }

p {
  text-align: justify; }

.clearBoth {
  zoom: 1; }
  .clearBoth::before, .clearBoth::after {
    content: '';
    display: table;
    clear: both;
    visibility: hidden;
    overflow: hidden; }

::-webkit-input-placeholder {
  color: gray; }

:-ms-input-placeholder {
  color: gray; }

::placeholder {
  color: gray; }

#app {
  height: 100%;
  width: 100%;
  overflow: hidden;
  padding: 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  background: linear-gradient(45deg, #726193 20%, #e37b7c 60%, #ffe4b4); }
